import Vue from 'vue'
const state = {
    shop: {}
}

const getters = {
    shop(state) {
        return state.shop
    }
}

const mutations = {
    shop(state, flag) {
        if (state.shop[flag.type]) {
            state.shop[flag.type].num++

        } else {
            Vue.set(state.shop, flag.type, flag)
        }

    },
    deleteItem(state, type) {
        delete state.shop[type]
    }
}

const actions = {
    asyncshop(context, flag) {
        context.commit('shop', flag)

    },
    asyncDelete(context, type) {
        context.commit('deleteItem', type)
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}