const state = {
    menu: {}
}

const getters = {
    getmenu(state) {
        return state.menu
    }
}

const mutations = {
    menu(state, flag) {
        state.menu = flag
    }
}

const actions = {
    asyncmenu(context, flag) {
        context.commit('menu', flag)
    },
    fetchMenu(context) {
        const res = axios.get('/menu.json')
        console.log(res);
        // context.commit('menu', res.data)
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}