const state = {
    isLogin: false
}

const getters = {
    getIsLogin(state) {
        return state.isLogin
    }
}

const mutations = {
    isLogin(state, flag) {
        state.isLogin = flag
    }
}

const actions = {
    asyncIsLogin(context, flag) {
        context.commit('isLogin', flag)
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}