import Vue from 'vue'
import Vuex from 'vuex'
import user from './module/user'
import isLogin from './module/isLogin'
import menu from './module/menu'
import shop from './module/shop'
Vue.use(Vuex)

export default new Vuex.Store({
    modules: {
        user,
        isLogin,
        menu,
        shop
    }
})