const state = {
    user: {}
}

const getters = {
    getuser(state) {
        return state.user
    }
}

const mutations = {
    user(state, flag) {
        state.user = flag
    }
}

const actions = {
    asyncuser(context, flag) {
        context.commit('user', flag)
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}