import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import orderingGuide from './components/about/OrderingGuide'
import history from './components/about/History'
import delivery from './components/about/Delivery'
Vue.use(Router)
const route = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [{
            path: '/',
            name: 'home',
            components: {
                default: Home,
                orderingGuide,
                history,
                delivery

            }
        },
        {
            path: '/register',
            name: 'register',
            component: () =>
                import ("./views/Register.vue")
        },
        {
            path: '/login',
            name: 'login',
            component: () =>
                import ('./views/Login')
        },
        {
            path: '/admin',
            name: 'admin',
            component: () =>
                import ('./views/Admin')
        },
        {
            path: '/menus',
            name: 'menus',
            component: () =>
                import ('./views/Menus')
        },
        {
            path: '/about',
            name: 'about',
            redirect: '/personName',
            component: () =>
                import ('./views/About'),
            children: [{
                    path: '/history',
                    name: 'history',
                    component: () =>
                        import ('./components/about/History')
                },
                {
                    path: '/delivery',
                    name: 'delivery',
                    component: () =>
                        import ('./components/about/Delivery')
                },
                {
                    path: '/orderingGuide',
                    name: 'orderingGuide',
                    component: () =>
                        import ('./components/about/OrderingGuide')
                },
                {
                    path: '/personName',
                    name: 'personName',
                    redirect: '/personName1',
                    component: () =>
                        import ('./components/about/PersonName'),
                    children: [{
                            path: '/phone',
                            name: 'phone',
                            component: () =>
                                import ('./components/about/Phone')
                        },
                        {
                            path: '/personName1',
                            name: 'personName1',
                            component: () =>
                                import ('./components/about/personName1')
                        },
                    ]
                },
            ]
        },
        {
            path: '*',
            redirect: '/'
        }

    ]
})

// route.beforeEach((to, from, next) => {
//     const isLogin = localStorage.user ? true : false;
//     next()
// })

export default route